.banner {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 30%;
	width: 100vw;
	padding: 50px 40px;
	background-color: #e5e5e5;
}
.name {
	margin-bottom: 20px;
}
.bio {
	margin-bottom: 15px;
}
.banner_info {
	display: flex;
	flex-direction: column;
}
.image {
	max-width: 40%;
	margin-right: 30px;
}
.image img {
	width: 200px;
	height: 200px;
	border-radius: 50%;
}
.extra-info div {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0;
	margin: 0 25px 0 0;
}
.extra-info {
	display: flex;
	align-items: center;
	margin-top: 10px;
}
.extra-info div img {
	padding: 0;
	margin: 0;
}

/*Logo Image*/
.image-container {
	display: flex;
	width: 50%;
	cursor: pointer;
}
.image-container img {
	width: 20px;
	height: 20px;
	margin-right: 10px;
	border-radius: 50%;
}

@media screen and (max-width: 768px) {
	.banner {
		height: 40%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.banner_info {
		margin-top: 20px;
		justify-content: center;
		align-items: center;
	}
	.image {
		max-width: 60%;
	}
	.image-container a {
		margin: 0 10px;
	}
	.image-container {
		margin-bottom: 10px 10px;
		width: 100%;
	}
	.image img {
		width: 100px;
		height: 100px;
	}
	.extra-info div {
		margin: 0 10px 0 0;
	}
	.extra-info {
		margin-top: 0;
	}
	.extra-info div img {
		width: 15px;
		height: 15px;
	}
}
