.header {
	display: flex;
	background-color: #2f2e41;
	color: white;
	height: 600px;
}

.image {
	max-height: 100%;
	width: 50%;
}
.title {
	margin: auto 100px;
	font-size: 72px;
	color: #fff;
	font-family: "Numito Sans";
}

@media (max-width: 768px) {
	.header {
		display: flex;
		flex-direction: column-reverse;
		height: 400px;
		padding: 0 auto;
		justify-content: space-around;
	}
	.image {
		max-height: 50%;
		max-width: 100%;
		margin: 0 auto;
	}
	.title {
		font-size: 36px;
		text-align: center;
		margin: 0 auto;
		margin-bottom: 50px;
	}
}
