.dev-details {
	display: grid;
	grid-template-columns: 1fr 1fr;
	max-width: 75%;
	margin: 0 auto;
}

.dev-description {
	display: flex;
	align-items: center;
	margin-bottom: 80px;
}

.image {
	height: 100px;
	width: 100px;
	margin-right: 50px;
	border-radius: 50%;
}
.description {
	margin-right: 20px;
}
.link {
	cursor: pointer;
	margin-right: 5px;
}

/*SeachBAr*/
.search {
	position: relative;
	width: 800px;
	height: 80px;
	background: #d0cde1;
	border-radius: 15px;
	margin: 80px auto 100px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.input-search {
	width: 709px;
	height: 60.49px;
	font-size: 24px;
	font-style: normal;
	font-weight: 400;
	line-height: 33px;

	background: #ffffff;
	border-radius: 15px;
	padding: 10px;
	border-style: none;
}
.input-search:focus {
	outline: none;
}

.btn-search {
	height: 40.525611877441406px;
	width: 41.53874969482422px;
	cursor: pointer;
	margin-right: 24px;
}
.form {
	display: flex;
	align-items: center;
	justify-content: space-around;
}
.btn {
	border: none;
	width: 0;
	padding: 0;
}

@media screen and (max-width: 768px) {
	.search {
		width: 278px;
		height: 27.77px;
		background: #d0cde1;
		border-radius: 15px;
		margin: 20px auto;
	}
	.input-search {
		width: 240.08px;
		height: 20.99px;
		background: #ffffff;
		border-radius: 15px;
		font-size: 12px;
		margin: 0 4px 0 0px;
	}
	.btn-search {
		height: 14px;
		width: 14px;
	}
	.dev-details {
		grid-template-columns: 1fr 1fr;
	}
	.image {
		height: 50px;
		width: 50px;
		margin-right: 15px;
	}
	.description {
		font-size: 14px;
		margin-right: 5px;
	}
	.dev-description {
		margin-bottom: 40px;
	}
}
