.footer {
	display: flex;
	background-color: #000;
	color: white;
	height: 80px;
	min-width: 100%;
	position: relative;
	bottom: 0;
	justify-content: center;
	align-items: center;
	font-size: 24px;
	color: rgba(255, 255, 255, 0.37);
}

a {
	text-decoration: none;
	color: white;
}
.heart {
	color: red;
	margin: auto 0;
}
@media screen and (max-width: 768px) {
	.footer {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-size: 16px;
		height: 60px;
		position: relative;
		bottom: 0;
		letter-spacing: 0.2em;
	}
	.heart {
		margin: 10px 0 0 0;
	}
}
