.buttonStyle {
	display: block;
	background-color: #7c639f;
	border-radius: 15px;
	color: white;
	border: none;
	cursor: pointer;
	margin: 0 auto 50px;
	text-align: center;
	height: 80px;
	font-size: 36px;
}
.button-text {
	margin: 0 72px;
}

@media screen and (max-width: 768px) {
	.buttonStyle {
		width: 180px;
		height: 30px;
		padding: 0;
		margin: 0 auto 30px;
	}
	.button-text {
		font-size: 20px;
		width: 100%;
		height: 100%;
		margin: 0;
		position: relative;
		top: -10px;
	}
}
