.form-container {
	margin: auto;
	margin-top: 2%;
	margin-bottom: 5%;
	width: 40%;
	background-color: white;

	padding: 30px 50px;
	border-radius: 10px;
}
.horizontal-rule {
	margin: 15px auto;
	margin-bottom: 30px;
}
.image {
	display: inline-block;
	width: 25px;
	height: 25px;
	border-radius: 50%;
}
.title {
	font-family: "Nunito Sans", sans-serif;
	margin-left: 5px;
	font-size: 20px;
}

.form-title {
	font-size: 32px;
}
.input-field {
	display: block;
	width: 80%;
	margin-bottom: 10px;
	padding: 2px 10px;
}
.input-field:focus {
	outline: none;
}
.label {
	display: flex;
	margin: 20px 0 8px 0;
	justify-items: center;
	align-items: center;
}
.form {
	position: relative;
	padding-bottom: 4%;
}
.submit {
	position: absolute;
	right: 0px;

	width: 100px;
	border-radius: 5px;
	border-style: none;
	padding: 0.5em;
	background-color: #7c639f;
	color: white;
	cursor: pointer;
}
.cancel {
	display: inline-block;
	cursor: pointer;
	position: absolute;
	padding: 0.5em;
	right: 110px;
}

@media screen and (max-width: 768px) {
	.form-container {
		width: 100%;
	}
	.form-title {
		font-size: 24px;
	}
	.input-field {
		width: 100%;
	}
	.submit {
		width: 80px;
	}
	.cancel {
		position: absolute;
		right: 80px;
	}
}
