* {
	box-sizing: border-box;
	margin: 0;
}
body {
	box-sizing: border-box;
	min-height: 100vh;
	background-color: #e5e5e5;
	font-family: "Nunito Sans", sans-serif;
	padding: 0;
}
.heading {
	width: 100%;
	font-size: 56px;
	color: #353d56;
	margin: 50px auto;
	text-align: center;
}

hr {
	margin: auto 50px;
}

.question {
	text-align: center;
	margin: 50px auto;
	font-size: 30px;
	color: #3f3d56;
}
@media screen and (max-width: 768px) {
	.heading {
		font-size: 20px;
		font-weight: 600;
		margin: 20px auto;
	}
	.question {
		font-size: 16px;
		margin: 40px auto;
	}
}
